import React from 'react';
import PropTypes from 'prop-types';

export default function Checkmark (props) {
  const isChecked = props.checked && !props.indeterminate && !props.empty;
  const PUID = 'cb_' + (Math.random() * 1e8).toString(32);
  return (
    <svg
      xmlns="http://www.w3.org/2000/svg"
      version="1.1"
      viewBox="0 0 20 20"
      height={props.size}
      width={props.size}
      style={{
        display: 'inline-block',
        verticalAlign: 'middle',
        cursor: props.disabled ? 'not-allowed' : 'pointer'
      }}
      >
      <defs>
        <linearGradient id={PUID} gradientTransform="rotate(90)">
          <stop offset="0" stopColor={isChecked && !props.disabled ? "#cdd4ed" : "#fff"} />
          <stop offset="1" stopColor={isChecked && !props.disabled ? "#9ca9db" : "#ddd"} />
        </linearGradient>
      </defs>
      <g opacity={props.disabled ? 0.6 : 1}>
        {!props.empty && (
          <g>
            <rect
              fillOpacity="0.04"
              width="13"
              height="13"
              x="5"
              y="5"
              rx="3.6"
              ry="3.6" />
            <rect
              fillOpacity="0.06"
              width="12"
              height="12"
              x="5"
              y="5"
              rx="3.4"
              ry="3.4" />
          </g>
        )}
        <rect
          ry="2.5"
          rx="2.5"
          y="4.5"
          x="4.5"
          height="11"
          width="11"
          strokeDasharray={props.empty ? "1.3 1.2" : null}
          fill={props.empty ? 'none' : `url(#${PUID})`}
          stroke={isChecked && !props.disabled ? "#4f67be" : "#999"}
          />
        {props.indeterminate && (
          <rect
            y="9"
            x="7"
            height="2"
            width="6" />
        )}
        {isChecked && (
          <path
            d="M6,9.5 L9.5,12.5 L16,5"
            fill="none"
            stroke="black"
            strokeWidth="1.7"
            />
        )}
      </g>
    </svg>
  );
}

Checkmark.propTypes = {
  size: PropTypes.number,
  checked: PropTypes.bool,
  disabled: PropTypes.bool,
  indeterminate: PropTypes.bool,
  empty: PropTypes.bool
};

Checkmark.defaultProps = {
  size: 28,
  checked: false,
  disabled: false,
  indeterminate: false,
  empty: false
};
