import React from 'react';
import PropTypes from 'prop-types';
import Checkmark from './Checkmark';
import css from './styles.css';

export default class Combobox extends React.Component {

  static propTypes = {
    className: PropTypes.string,
    children: PropTypes.node
  };

  static defaultProps = {
  };

  constructor (props) {
    super(props);
    this.state = {
      value: '',
      drawer: false,
      optionIndex: -1,
      options: props.options
    };
    this.onChange = this.onChange.bind(this);
    this.onKeyPress = this.onKeyPress.bind(this);
    this.onKeyDown = this.onKeyDown.bind(this);
    this.onDrawer = this.onDrawer.bind(this);
    this.onAction = this.onAction.bind(this);
    this.onHideDrawer = this.onHideDrawer.bind(this);
    this.elm = React.createRef();
  }

  componentDidMount () {
    document.addEventListener('click', this.onHideDrawer);
  }

  componentWillUnmount () {
    document.removeEventListener('click', this.onHideDrawer);
  }

  onHideDrawer (e) {
    if (this.elm.current && !this.elm.current.contains(e.target)) {
      this.setState({ drawer: false });
    }
  }

  onChange (e) {
    const val = e.target.value;
    let cand = this.props.options;
    let optionIndex = this.state.optionIndex;
    // can we filter 
    if (val && /^=/.test(val)) {
      // is expression
      cand = [];
      optionIndex = -1;
    }
    else if (val) {
      const curr = this.state.options[optionIndex];
      const lc = val.toLowerCase();
      cand = this.props.options.filter(d => d.value.toLowerCase().startsWith(lc));
      // try to keep the current selection if we can
      if (curr) { optionIndex = cand.indexOf(curr); }
    }
    this.setState({
      value: val,
      options: cand,
      drawer: cand.length || this.state.drawer,
      optionIndex: optionIndex
    });
  }

  onKeyPress (e) {
  }

  onKeyDown (e) {
    const optionIndex = this.state.optionIndex;
    const opts = this.state.options;
    switch (e.keyCode) {
      case 40: // arrow down
        if (optionIndex === -1) {
          this.setState({ optionIndex: 0, drawer: true });
        }
        else if (optionIndex >= 0 && optionIndex < opts.length - 1) {
          this.setState({ optionIndex: optionIndex + 1, drawer: true });
        }
        break;
      case 38: // arrow up
        if (optionIndex > 0) {
          this.setState({ optionIndex: optionIndex - 1, drawer: true });
        }
        else if (optionIndex === 0) {
          this.setState({ optionIndex: -1, drawer: false });
        }
        break;
      case 39: // arrow forward
        // select
        break;
      case 37: // arrow back
        // noop?
        break;
      case 48: // =
        // close drawer
        break;
      case 27: // escape
        this.setState({ value: '', optionIndex: -1, drawer: false });
        break;
      case 9: // tab
        // autocomplete
        e.preventDefault();
        if (opts.length) {
          // select auto-closes the drawer
          this.select(opts[0]);
        }
        break;
      case 13: // enter
        if (optionIndex >= 0 && optionIndex < opts.length) {
          // select auto-closes the drawer
          this.select(opts[optionIndex]);
        }
      case 32: // spacebar
        break;
    }
  }

  onAction (e) {
    const val = this.state.value.toUpperCase();
    if (val === 'TRUE') {
      this.setState({ value: 'FALSE' });
    }
    else if (val === 'FALSE' || !val) {
      this.setState({ value: 'TRUE' });
    }
  }

  onDrawer (e) {
    this.setState({
      drawer: !this.state.drawer
    }, () => {
      this.inputElm.focus();
    });
  }


  select (s) {
    this.setState({
      optionIndex: -1,
      value: s.value,
      drawer: false
    });
  }

  renderOptions () {
    if (!this.state.drawer) {
      return null;
    }
    let opts = this.state.options;
    if (!opts.length) {
      opts = this.props.options.filter(d => !d.value);
    }
    return (
      <div className={css.options}>
        {opts.map((d, i) => (
          <div
            key={d.value}
            className={css.option + ' ' + (this.state.optionIndex === i ? css.selected : '')}
            onMouseDown={e => e.preventDefault()}
            onClick={e => {
              e.preventDefault();
              this.select(d);
            }}
            >
            {d.value || '\u00a0'}
          </div>
        ))}
      </div>
    );
  }

  renderDrawerButton () {
    return (
      <button
        className={css.drawer}
        onClick={this.onDrawer}
        tabIndex="-1"
        onMouseDown={e => e.preventDefault()}
        >
        <svg version="1.1" viewBox="0 0 10 10" height="10" width="10">
          <path d="M0,1 L5,9 L10,1 Z" />
        </svg>
      </button>
    );
  }

  renderActionButton () {
    const val = this.state.value.toUpperCase();
    return (
      <span
        className={css.action}
        role="button"
        tabIndex="0"
        onMouseDown={e => e.preventDefault()}
        onClick={this.onAction}
        >
        <Checkmark
          checked={val === 'TRUE'}
          indeterminate={!!(val && val !== 'TRUE' && val !== 'FALSE')}
          disabled={!!(val && val !== 'TRUE' && val !== 'FALSE')}
          />
      </span>
    );
  }

  render () {
    return (
      <div
        onClick={e => e.preventDefault()}
        className={css.root}
        ref={this.elm}
        >
        <label
          htmlFor="study1"
          >
          {this.props.label}
        </label>
        <div
          className={css.control}
          >
          <input
            id="study1"
            type="text"
            placeholder={this.props.placeholder}
            ref={elm => this.inputElm = elm}
            value={this.state.value || ''}
            onChange={this.onChange}
            onKeyPress={this.onKeyPress}
            onKeyDown={this.onKeyDown}
            />
          {this.renderDrawerButton()}
          {this.renderOptions()}
        </div>
        {this.renderActionButton()}
      </div>
    );
  }

}
